<template>

    <div class="form">
        <h1>Namn</h1>
        <div class="txtb">
            <input type="text" id="firstName"
                   ref="firstName"
                   @click="focused('lastName')"
                   @blur="blurred('firstName')"

                   :class="{focus :focusedClass === 'firstName'}">
            <span data-placeholder="Förnamn"></span>
        </div>
        <div class="txtb">
            <input type="text" id="lastName"
                   ref="lastName"
                   @click="focused('firstName')"
                   @blur="blurred('lastName')"
                   :class="{focus :focusedClass === 'lastName'}">
            <span data-placeholder="Efternamn"></span>
        </div>
    </div>


</template>

<script>
    export default {
        name: "form1.vue",
        data() {
            return {
                focusedInput: ''
            }
        },
        methods: {
            focused: function (inp) {
                console.log("inp", inp + '  ' + this.focusedInput);
                console.log(this.focusedInput);

                this.focusedInput = inp;

                let docSelection = inp;

                /*
                switch (inp) {
                    case 'firstName':
                        docSelection = 'lastName';
                        break;
                    case 'lastName':
                        docSelection = 'firstName';
                        break;
                }

                 */


                if (docSelection !== '') {
                    console.log('focus', docSelection);
                    this.$refs[docSelection].focus();
                    //document.getElementById(docSelection).focus();
                }
            },
            blurred: function (inp) {
                if (this.focusedInput === inp)
                {
                    this.focusedInput = '';
                }

            }
        },
        computed: {
            focusedClass: function () {

                return this.focusedInput;

            }
        }
    }
</script>

